<template>
  <div id="app">
    <Header />
    <Toast />
    <ModalInfo />
    <ConfirmModal
      :confirmTitle="'REMOVE MOVIE:'"
      :confirmMsg="'Are you sure remove  ' + removeMovieTitle + '?'"
      :active="toggleDeleteMovie"
    />
    <Loader />
    <PosterBg :posterBg="posterBg" />
    <MoviesList :moviesList="moviesList" @changePoster="onChangePoster" />
    <Pagination
      :current-page="currentPage"
      :per-page="moviesPerPage"
      :total-movies="moviesLength"
    />
  </div>
</template>

<script>
import { mapGetters } from "vuex";

import MoviesList from "@/components/MoviesList";
import PosterBg from "@/components/PosterBg";
import Pagination from "@/components/Pagination";
import Loader from "@/components/Loader";
import ConfirmModal from "@/components/ConfirmModal";
import Header from "@/components/Header";
import ModalInfo from "@/components/ModalInfo";
import Toast from "@/components/Toast";

export default {
  name: "App",
  components: {
    MoviesList,
    PosterBg,
    Pagination,
    Loader,
    ConfirmModal,
    Header,
    ModalInfo,
    Toast,
  },
  data() {
    return {
      posterBg: "",
    };
  },
  computed: {
    ...mapGetters("movies", [
      "moviesList",
      "currentPage",
      "moviesPerPage",
      "moviesLength",
      "toggleDeleteMovie",
      "removeMovieTitle",
    ]),
  },
  methods: {
    onChangePoster(poster) {
      this.posterBg = poster;
      console.log(this.posterBg);
    },
  },
};
</script>

