import mutations from "../mutations"

const { TOGGLE_MODAL_INFO, MOVIE_INFO } = mutations;

export default {
    state: {
        modalIsAcive: false,
        movieInfo: {}
    },
    getters: {
        modalIsAcive: ({ modalIsAcive }) => modalIsAcive,
        movieInfo: ({ movieInfo }) => movieInfo,
    },
    mutations: {
        [TOGGLE_MODAL_INFO](state, bool) { state.modalIsAcive = bool },
        [MOVIE_INFO](state, movieInfo) { state.movieInfo = movieInfo }
    },
    actions: {
        toggleModalInfo({ commit }, bool) {
            commit(TOGGLE_MODAL_INFO, bool);
            const scrollSize = window.innerWidth - document.querySelector('#app').offsetWidth + 'px';
            if (bool) {
                document.body.style.overflow = 'hidden'
                document.body.style.paddingRight = scrollSize;
            }
            else {
                document.body.style.paddingRight = '';
                document.body.style.overflow = 'auto'
            }
        },
        getMovieInfo({ commit }, movieInfo) {
            console.log(movieInfo);
            commit(MOVIE_INFO, movieInfo);
        }
    },
}