<template>
  <BContainer class="main__container">
    <h1 class="movies-app-title pt-5 mb-5">{{ setTitile }}</h1>
    <BRow>
      <BCol
        cols="12"
        sm="6"
        md="4"
        lg="3"
        v-for="(movie, idx) in moviesList"
        :key="idx"
      >
        <MovieItem :movie="movie" @mouseover.native="getPoster(movie.Poster)" />
      </BCol>
    </BRow>
  </BContainer>
</template>

<script>
import { mapGetters } from "vuex";
import MovieItem from "@/components/MovieItem";

export default {
  name: "MoviesList",
  props: {
    moviesList: {
      type: Object,
      default: () => ({}),
    },
  },
  components: { MovieItem },
  computed: {
    ...mapGetters("movies", ["isSearch"]),
    setTitile() {
      if (this.isSearch) {
        return "Search result";
      } else {
        return "IMDB Top 250";
      }
    },
  },
  methods: {
    getPoster(poster) {
      this.$emit("changePoster", poster);
    },
  },
};
</script>

