<template>
  <div class="confirm-plug" :class="setStatus" @click="closeConfirm">
    <div class="confirm-wrapper" ref="confirm-wrapper">
      <div class="confirm-title">{{ confirmTitle }}</div>
      <div class="confirm-content">{{ confirmMsg }}</div>
      <div class="confirm-btns">
        <button class="confirm-btn cancel" @click="onCancelRemoveMovie">
          Cancel
        </button>
        <button class="confirm-btn success" @click="onRemoveMovie">OK</button>
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions } from "vuex";

export default {
  name: "ConfirmModal",
  props: {
    confirmTitle: {
      type: String,
      default: "",
    },
    confirmMsg: {
      type: String,
      default: "",
    },
    active: {
      type: Boolean,
      default: false,
    },
  },
  computed: {
    setStatus() {
      return this.active ? "active" : "";
    },
  },
  methods: {
    ...mapActions("movies", ["removeMovie", "cancelRemoveMovie"]),
    ...mapActions(["showToast"]),
    onRemoveMovie() {
      this.removeMovie();
      this.showToast({
        msg: "Movie removed!",
        status: "success",
      });
    },
    onCancelRemoveMovie() {
      this.cancelRemoveMovie();
    },
    closeConfirm(event) {
      if (event.target !== this.$refs["confirm-wrapper"]) {
        this.cancelRemoveMovie();
      }
    },
  },
};
</script>

