<template>
  <div class="poster-bg" :style="setBG"></div>
</template>

<script>
export default {
  name: "PosterBg",
  props: {
    posterBg: {
      type: String,
      default: "",
    },
  },
  data() {
    return {
      defaultBG: "linear-gradient( 45deg, rgb(0,3,38) 0%, rgb(82,15,117) 0%)",
    };
  },
  computed: {
    setBG() {
      return {
        "background-image": this.posterBg
          ? `url(${this.posterBg})`
          : this.defaultBG,
      };
    },
  },
};
</script>

