<template>
  <div
    class="modal-info-block"
    :class="statusModal"
    @click="closeModal"
    ref="modal-info-block"
  >
    <div class="modal-info-wrapper total-movie" v-if="!isSearch">
      <div class="modal-info-img">
        <img :src="this.movieInfo.Poster" alt="" class="modal-img" />
      </div>
      <div class="modal-info-content">
        <div class="info-wrapper movie-title-wrapper">
          <h5 class="info-movie movie-title">{{ this.movieInfo.Title }}</h5>
          <span class="info-label movie-title-label">movie title</span>
        </div>
        <div class="modal-info-desc">
          <div class="info-wrapper movie-desc-wrapper">
            <h5 class="info-movie movie-desc">
              {{ this.movieInfo.Country }}
            </h5>
            <span class="info-label movie-desc-label">country</span>
          </div>
          <div class="info-wrapper movie-desc-wrapper">
            <h5 class="info-movie movie-desc">
              {{ this.movieInfo.Year }}
            </h5>
            <span class="info-label movie-desc-label">year</span>
          </div>
          <div class="info-wrapper movie-desc-wrapper">
            <h5 class="info-movie movie-desc">
              {{ this.movieInfo.Released }}
            </h5>
            <span class="info-label movie-desc-label">released</span>
          </div>
          <div class="info-wrapper movie-desc-wrapper">
            <h5 class="info-movie movie-desc">
              {{ this.movieInfo.Genre }}
            </h5>
            <span class="info-label movie-desc-label">genre</span>
          </div>
          <div class="info-wrapper movie-desc-wrapper">
            <h5 class="info-movie movie-desc">
              {{ this.movieInfo.Language }}
            </h5>
            <span class="info-label movie-desc-label">language</span>
          </div>
          <div class="info-wrapper movie-desc-wrapper">
            <h5 class="info-movie movie-desc">
              {{ this.movieInfo.Writer }}
            </h5>
            <span class="info-label movie-desc-label">writer</span>
          </div>
          <div class="info-wrapper movie-desc-wrapper">
            <h5 class="info-movie movie-desc">
              {{ this.movieInfo.Director }}
            </h5>
            <span class="info-label movie-desc-label">director</span>
          </div>
          <div class="info-wrapper movie-desc-wrapper">
            <h5 class="info-movie movie-desc">
              {{ this.movieInfo.Actors }}
            </h5>
            <span class="info-label movie-desc-label">actors</span>
          </div>
          <div class="info-wrapper movie-desc-wrapper">
            <h5 class="info-movie movie-desc">
              {{ this.movieInfo.Runtime }}
            </h5>
            <span class="info-label movie-desc-label">runtime</span>
          </div>
          <div class="info-wrapper movie-desc-wrapper">
            <h5 class="info-movie movie-desc movie-desc-plot">
              {{ this.movieInfo.Plot }}
            </h5>
            <span class="info-label movie-desc-label">plot</span>
          </div>
        </div>
        <button class="modal-close-btn" @click="onBtnClose">Close</button>
      </div>
    </div>
    <div class="modal-info-wrapper total-movie" v-else>
      <div class="modal-info-img">
        <img :src="this.movieInfo.Poster" alt="" class="modal-img" />
      </div>
      <div class="modal-info-content">
        <div class="info-wrapper movie-title-wrapper">
          <h5 class="info-movie movie-title">{{ this.movieInfo.Title }}</h5>
          <span class="info-label movie-title-label">movie title</span>
        </div>
        <div class="modal-info-desc">
          <div class="info-wrapper movie-desc-wrapper">
            <h5 class="info-movie movie-desc">
              {{ this.movieInfo.Type }}
            </h5>
            <span class="info-label movie-desc-label">type</span>
          </div>
          <div class="info-wrapper movie-desc-wrapper">
            <h5 class="info-movie movie-desc">
              {{ this.movieInfo.Year }}
            </h5>
            <span class="info-label movie-desc-label">year</span>
          </div>
        </div>
        <button class="modal-close-btn" @click="onBtnClose">Close</button>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";

export default {
  name: "ModalInfo",
  computed: {
    ...mapGetters(["modalIsAcive", "movieInfo"]),
    ...mapGetters("movies", ["isSearch"]),
    statusModal() {
      return this.modalIsAcive ? "active" : "";
    },
  },
  methods: {
    ...mapActions(["toggleModalInfo"]),
    closeModal(e) {
      if (e.target === this.$refs["modal-info-block"]) {
        this.toggleModalInfo(false);
      }
    },
    onBtnClose() {
      this.toggleModalInfo(false);
    },
  },
};
</script>

