import mutations from "../mutations"
const { TOGGLE_TOAST } = mutations;

export default {
    state: {
        isActiveToast: false,
    },
    getters: {
        isActiveToast: ({ isActiveToast }) => isActiveToast
    },
    mutations: {
        [TOGGLE_TOAST](state, bool) { state.isActiveToast = bool }
    },
    actions: {
        showToast({ commit }, { msg, status }) {
            let toastRoot = document.querySelector('.toast-block');
            commit(TOGGLE_TOAST, true);
            let toastElement = document.createElement('div');
            toastElement.innerHTML = msg;
            toastElement.classList.add('custom-toast');
            toastElement.classList.add(`custom-toast-${status}`);
            toastRoot.insertAdjacentElement('afterbegin', toastElement);
            setTimeout(() => {
                toastElement.remove()
            }, 2000);
            commit(TOGGLE_TOAST, false);
        }
    },
}