<template>
  <BPagination
    class="movie-pagination"
    v-model="changePage"
    :total-rows="totalMovies"
    :per-page="perPage"
  ></BPagination>
</template>

<script>
import { mapActions } from "vuex";

export default {
  name: "Pagination",
  props: {
    currentPage: {
      type: Number,
      default: 1,
    },
    perPage: {
      type: Number,
      default: 1,
    },
    totalMovies: {
      type: Number,
      default: 1,
    },
  },
  computed: {
    changePage: {
      get() {
        return this.currentPage;
      },
      set(page) {
        this.changeCurrentPage(page);
      },
    },
  },
  methods: {
    ...mapActions("movies", ["changeCurrentPage"]),
  },
};
</script>

