<template>
  <div class="movie-item-card mb-4" :style="setPoster">
    <div class="movie-info-wrap">
      <div class="movie-info">
        <h3 class="movie-title">{{ movie.Title }}</h3>
        <span class="movie-year"> {{ movie.Year }} </span>
      </div>
      <div class="movie-controllers-wrap">
        <BButton class="movie-btn" variant="outline-light" @click="onInfoMovie"
          >Info</BButton
        >
        <BButton
          class="movie-btn"
          variant="outline-light"
          @click="onRemoveMovie"
          >Remove</BButton
        >
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions } from "vuex";

export default {
  name: "MovieItem",
  props: {
    movie: {
      type: Object,
      require: true,
    },
  },
  computed: {
    setPoster() {
      return {
        "background-image": `url(${this.movie.Poster})`,
      };
    },
  },
  methods: {
    ...mapActions("movies", ["askDeleteMovie"]),
    ...mapActions(["toggleModalInfo", "getMovieInfo", "showToast"]),
    onInfoMovie() {
      this.toggleModalInfo(true);
      this.getMovieInfo(this.movie);
    },
    onRemoveMovie() {
      this.askDeleteMovie({
        active: true,
        id: this.movie.imdbID,
        title: this.movie.Title,
      });
    },
  },
};
</script>

