<template>
  <header class="header">
    <div class="container header-container">
      <h2 class="header-title">IMDB Movies</h2>
      <input class="header-input" type="text" v-model="inputSearch" />
    </div>
  </header>
</template>

<script>
import { mapActions } from "vuex";

export default {
  name: "Header",
  data() {
    return {
      inputSearch: "",
    };
  },
  watch: {
    inputSearch: "handlerInputSearch",
  },
  methods: {
    ...mapActions("movies", ["searchMovies"]),
    handlerInputSearch(value) {
      this.searchMovies(value);
    },
  },
};
</script>

