import mutations from "../mutations";

const { SHOW_LOADER } = mutations;


export default {
    state: {
        isShow: false,
    },
    getters: {
        loaderStatus: ({ isShow }) => isShow
    },
    mutations: {
        [SHOW_LOADER](state, bool) { state.isShow = bool }
    },
    actions: {
        changeLoader({ commit }, status) {
            commit(SHOW_LOADER, status)
        }
    },
}