import axios from '@/plugins/axios'
import top250MoviesIDs from '../mock/imdb_top250'
import mutations from '../mutations';

const { MOVIES, CURRENT_PAGE, REMOVE_MOVIE, TOGGLE_DELETE_MOVIE, REMOVE_MOVIE_ID, REMOVE_MOVIE_TITLE, TOGGLE_SEARCH, SET_SEARCH_LIST } = mutations;



function serializeMovies(movies) {
    return movies.reduce((acc, movie) => {
        acc[movie.imdbID] = movie;
        return acc;
    }, {})
}

export default {
    namespaced: true,
    state: {
        moviesIDs: top250MoviesIDs,
        currentPage: 1,
        moviesPerPage: 12,
        moviesList: {},
        toggleDeleteMovie: false,
        removeMovieID: '',
        removeMovieTitle: '',
        isSearch: false,
        searchMoviesList: {}
    },
    getters: {
        moviesIDs: ({ moviesIDs }) => moviesIDs,
        currentPage: ({ currentPage }) => currentPage,
        moviesPerPage: ({ moviesPerPage }) => moviesPerPage,
        slicedMovies: ({ moviesIDs }) => (from, to) => moviesIDs.slice(from, to),
        moviesList: ({ moviesList }) => moviesList,
        removeMovieID: ({ removeMovieID }) => removeMovieID,
        removeMovieTitle: ({ removeMovieTitle }) => removeMovieTitle,
        toggleDeleteMovie: ({ toggleDeleteMovie }) => toggleDeleteMovie,
        isSearch: ({ isSearch }) => isSearch,
        moviesLength: ({ moviesIDs, searchMoviesList, isSearch }) => {
            if (isSearch) {
                return searchMoviesList.length;
            } else {
                return moviesIDs.length;
            }
        },
        getMovieTitle: ({ moviesList }) => (id) => moviesList[id],
    },
    mutations: {
        [MOVIES](state, value) { state.moviesList = value },
        [CURRENT_PAGE](state, value) { state.currentPage = value },
        [TOGGLE_DELETE_MOVIE](state, bool) {
            state.toggleDeleteMovie = bool;
            if (bool) {
                const scrollWindowSize = window.innerWidth - document.documentElement.clientWidth;
                document.body.style.overflow = 'hidden';
                document.body.style.paddingRight = scrollWindowSize + 'px';

            } else {
                setTimeout(() => {
                    document.body.style.paddingRight = '';
                    document.body.style.overflow = 'auto';
                }, 400);
            }
        },
        [REMOVE_MOVIE](state, id) { state.moviesIDs.splice(id, 1) },
        [REMOVE_MOVIE_ID](state, id) { state.removeMovieID = id },
        [REMOVE_MOVIE_TITLE](state, title) { state.removeMovieTitle = title },
        [TOGGLE_SEARCH](state, bool) { state.isSearch = bool },
        [SET_SEARCH_LIST](state, list) { state.searchMoviesList = list },
    },
    actions: {
        initFetchMovies: {
            handler({ dispatch }) {
                dispatch('fetchMovies')
            },
            root: true
        },
        async fetchMovies({ getters, commit, dispatch }) {
            try {
                dispatch('changeLoader', true, { root: true });

                const { currentPage, moviesPerPage, slicedMovies } = getters;

                const from = currentPage * moviesPerPage - moviesPerPage;
                const to = currentPage * moviesPerPage;

                const request = slicedMovies(from, to).map(movieID => axios.get(`/?i=${movieID}`))

                const response = await Promise.all(request)

                const movies = serializeMovies(response);

                console.log(movies);

                commit(MOVIES, movies)
            } catch (error) {
                console.error(error);
            } finally {
                dispatch('changeLoader', false, { root: true });
            }
        },
        changeCurrentPage({ commit, dispatch }, numberPage) {
            commit(CURRENT_PAGE, numberPage);
            dispatch('fetchMovies');
        },

        askDeleteMovie({ commit }, { active, id, title }) {
            commit(TOGGLE_DELETE_MOVIE, active);
            commit(REMOVE_MOVIE_ID, id);
            commit(REMOVE_MOVIE_TITLE, title);
        },
        removeMovie({ getters, commit, dispatch }) {
            const { moviesIDs, removeMovieID } = getters;
            const idRemove = moviesIDs.findIndex(id => id === removeMovieID);
            commit(REMOVE_MOVIE, idRemove);
            dispatch('fetchMovies');
            commit(TOGGLE_DELETE_MOVIE, false);
        },
        cancelRemoveMovie({ commit }) {
            commit(TOGGLE_DELETE_MOVIE, false);
        },
        async searchMovies({ commit, dispatch }, searchValue) {
            try {
                if (searchValue) {
                    const response = await axios.get(`/?s=${searchValue}`);
                    if (response.Search) {
                        commit(TOGGLE_SEARCH, true);
                        commit(SET_SEARCH_LIST, response);
                        const searchMovies = serializeMovies(response.Search);
                        console.log(searchMovies);
                        console.log(response.Search);
                        commit(MOVIES, searchMovies);
                    }
                    else {
                        throw ('Movies not found!');
                    }
                }
                else {
                    dispatch('fetchMovies');
                    commit(TOGGLE_SEARCH, false);
                }
            }
            catch (error) {
                console.error(error)
            }
        }
    },
}